<script>

// 参考
// https://github.com/fengyuanchen/vue-qrcode/blob/master/src/index.js

import qrcode from "arale-qrcode";

export default {
  name: "qrcode",
  props: {
    render: "", // default
    text: {
      type: String,
      required: true
    },
    size: {
      type: Number,
      default: 200
    },
    level: { // 0=L7%, 1=M15%, 2=Q25%, 3=H30%
      type: Number,
      default: 3
    },
    bgcolor: {
      type: String,
      default: "#FFFFFF"
    },
    forecolor: {
      type: String,
      default: "#000000"
    },
    logoImg: {
      type: String,
      default: ""
    },
    logoSize: {
      type: Number,
      default: 50
    }
  },
  render(h) {
    return h("div", this.$slots.default);
  },
  watch: {
    $props: {
      deep: true,
      immediate: true,

      handler() {
        if (this.$el) {
          this.generate();
        }
      }
    }
  },
  methods: {
    generate() {
      while (this.$el.firstChild) {
        this.$el.removeChild(this.$el.firstChild);
      }
      this.$el.appendChild(
        new qrcode({
          text: this.text,
          render: this.render,
          size: this.size,
          correctLevel: this.level,
          background: this.bgcolor,
          foreground: this.forecolor,
          image: this.logoImg,
          imageSize: this.logoSize
        })
      );
    }
  },
  mounted() {
    this.generate();
  }
};
</script>
