<script>
import pricing from "../data/pricing";
import qrcode from "./qrcode";

export default {
  name: "PaymentSteps",
  components: {
    qrcode
  },
  props: {
    msg: String
  },
  data() {
    return {
      active: 0,
      msver: "1",
      msregion: "ms1",
      selectedPrice: pricing.ms1[0], // 默认为 GMS
      pricing: pricing,
      paymethod: "alipay",
      showAlipayQR: false,
      paypalPeriod: 1
    };
  },
  methods: {
    preStep() {
      if (this.active-- <= 0) this.active = 0;
    },
    nextStep() {
      if (this.active++ >= 4) this.active = 4;
    },
    gotoActivate() {
      this.nextStep();
      setTimeout(() => {
        window.open("/" + this.paymethod);
      }, 2000);
    },
    chosenGame(game) {
      // 选择游戏时，改变默认 selectedPrice 的值
      this.selectedPrice =
        game === "ms1" ? this.pricing.ms1[0] : this.pricing.ms2[0];
      this.msregion = game;
    },
    openPaypalWindow() {
      window.open("https://www.paypal.me/genment/" + this.paypalPrice + "usd");
    }
  },
  computed: {
    getPricing() {
      switch (this.msregion) {
        case "ms1r":
          return this.pricing.ms1r;
        case "ms2":
          return this.pricing.ms2;
        default:
          return this.pricing.ms1;
      }
    },
    isAlipay() {
      return this.paymethod === "alipay";
    },
    qrcodeUrl() {
      return this.isAlipay
        ? this.selectedPrice.alipayUrl
        : this.selectedPrice.wxpayUrl;
    },
    paypalPrice() {
      return (
        1.99 * this.paypalPeriod -
        1.99 * (this.paypalPeriod - 1) * 0.01 * (this.paypalPeriod - 1)
      ).toFixed(2);
    }
  }
};
</script>

